import uniRate from '@/components/uni-rate/uni-rate.vue';
import imageUpload from '@/components/image-upload/image-upload.vue';
import Utils from '@/common/utils.js'
export default {
	components:{
		uniRate,
		imageUpload
	},
	data() {
		return {
			evaluatItems:[
				{title:'产品质量',type:1},
				{title:'师傅服务态度',type:2},
				{title:'客服服务态度',type:3},
			],
			upImgArr:[],
			textValue:'',
			upImgBasic:{
				// 后端图片接口地址
				basicConfig: {
					// url: 'https://p.dns06.net.cn/index.php?m=Api&c=index&a=upload',
					url: 'https://www.xyzcwl.com/api/upload/uploadPicture'
				},
				// 图片数量
				maxCount:4,
				autoUpload:false
			},
			productAppraise:5,
			erectorAppraise:5,
			platformAppraise:5	
		}
	},
	onLoad(option){
		if(option.orderId){
			console.log(option.orderId)
			this.orderId = option.orderId;
		}
	},
	methods: {
		delImgInfo(res){
			console.log(res)
		},
		changeValue(e){
			console.log(e)
			switch(e.type){
				case '1':
					this.productAppraise = e.value;
					break;
				case '2':
					this.erectorAppraise = e.value;
					break;
				case '3':
					this.platformAppraise = e.value;
					break;
			}
		},
		getPic(res){
			
			if(!res) return;
			let imgArr = [];
			
			res.forEach(function(item){
				if(item){
					imgArr.push(item)
				}
			})
			if(imgArr.length == res.length){
				console.log('上传完毕')
				this.upImgArr = imgArr;
				this.submit();
			}else{
				console.log('上传失败')
			}
		},
		
		upImage:Utils.debounce(function(){
			this.$refs.uImage.upLoadImages(this.upImgBasic)
		},1000),
		submit(){
			let _this = this;
			this.$http.request({
				url:this.$api.OrderAppraise,
				data:{
					appraiseDescribe:_this.textValue,
					orderId:_this.orderId,
					picture:_this.upImgArr.join(','),
					erectorAppraise:_this.erectorAppraise,
					platformAppraise:_this.platformAppraise,
					productAppraise:_this.productAppraise	
				},
				method:'POST'
			}).then(res => {
				console.log(res)
				if(res.errno == 0){
					uni.navigateBack({
						
					});
				}
			})
		},
		
	}
}
